import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class PropertyMaintenance extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='Property Maintenance' />
        <div className='form_inputs'>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFM_031_Ongoing_Maintenance_Services' })}</div>
          <br />
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFM_031_Ongoing_Maintenance_Services', 'Other') &&
              <>
                <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFM_031_Ongoing_Maintenace_Services_explanation' })}</div>
                <br />
              </>
          }
        </div>
      </>
    );
  }
}
